<template>
  <div class="subcontainer">

    <h4> {{this.following.length}} following </h4>

    <div style="height:240px;width480px;border:1px solid #ccc;overflow:auto;">  
        <div v-if="following.length">
            <li v-for="user in following" v-bind:key="user">
                @{{user}}
            </li>
        </div>
        <div v-else>
          <p style="text-align: center;">No following.</p>
        </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { eventBus } from "../main";

export default {
  name: "FollowingList",

  components: {},

  data() {
    return {
      username: this.$cookie.get('fritter-auth'),
      following: []
    };
  },

  methods: {
    loadFollowing: function() {
      axios.get(`/api/user/${this.username}/following`)
        .then(response => {
          this.following = response.data.following;
        })
    },

    clearMessages: function() {
      setInterval(() => {
        this.success = "";
        this.error = "";
      }, 5000);
    }
  },

  created: function() {
    eventBus.$on("follow-user-success", () => {
      this.clearMessages();
      this.loadFollowing();
    });

    eventBus.$on("unfollow-user-success", () => {
      this.clearMessages();
      this.loadFollowing();
    });

    eventBus.$on("delete-user-success", () => {
      this.clearMessages();
      this.loadFollowing();
    });

    eventBus.$on("change-username-user-success", () => {
      this.clearMessages();
      this.loadFollowing();
    });
  },

  mounted: function() {
    this.loadFollowing();
  }
};
</script>